* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.wrap {
  width: 600px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

#datetime-picker,
.btn-start,
.timer {
  font-size: 30px;
}

#datetime-picker {
  width: 350px;
  margin-right: 30px;
}

.timer {
  display: flex;
  padding-top: 40px;
}

.field {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.value {
  font-size: 60px;
  color: red;
}
